#Display {
    padding-top: 35px;
}

#Display .MuiTooltip-popper {
    text-indent: initial;
}

#Display .title-container h1 {
    margin-bottom: 0;
    margin-right: 5px;
    width: fit-content;
    text-indent: .5em;
    display: inline-block;
}

#Display .title-container button {
    font-size: 1.3rem;
    vertical-align: baseline;
    margin: 0 5px;
}

#Display .title-container .download-torrent-btn {
    display: inline-block;
}

#Display .details-container {
    text-align: center;
    padding: 15px 7px 15px 15px;
}

#Display .details-container img {
    width: 100%;
    display: block;
    margin: 0 auto;
    border-radius: 5px;
}

#Display .info-container {
    margin-top: 10px !important;
    text-align: left;
}

#Display .info-container h2:first-child {
    margin: 0 0 10px;
}

#Display .info-container h2, #Display .info-container h4 {
    margin: 10px 0 0;
}

#Display .details-container table tr {
    border-bottom: 1px solid var(--dark-blue);
}

#Display .details-container table tr td:first-child {
    color: var(--blue);
}

#Display .details-container table tr td:nth-child(2) {
    padding-left: 10px;
}

#Display .details-container table td, #Display .main-container .tabs-container p {
    font-size: .95em;
}

#Display .user-buttons-container {
    margin-top: 10px;
    text-align: center;
}

#Display .user-buttons-container button {
    margin: 0 5px;
    padding: 5px 0;
    font-size: 1.3em;
}

#Display .main-container {
    padding: 15px 15px 15px 7px;
}

#Display .main-container .summery-container {
    margin: 5px 0;
}

#Display .main-container .summery-container:first-child {
    margin: 0 0 5px;
}

#Display .main-container .summery-container p {
    margin: 5px 0;
    text-align: center;
}

#Display .main-container .summery-container p:first-child {
    color: var(--blue);
}

#Display .main-container .summery-container p:nth-child(2) {
    font-size: 1.3em;
}

#Display .main-container .summery-container .genres-container .genre-spacing {
    margin: 0 15px;
    display: inline-block;
}

#Display .main-container .color-secondary p:first-child {
    color: var(--dark-magenta);
}

#Display .main-container .color-secondary p:nth-child(2) {
    color: var(--magenta);
    font-size: 1.3em;
}

#Display .main-container .color-secondary button {
    display: block;
    margin: 13px auto;
}

#Display .main-container .update-anime-episode-btn {
    font-size: 1rem;
    margin-left: 5px;
}

#Display .tabs-root {
    width: 100%;
}

#Display .main-container .tabs-container .list-icon {
    font-size: 1rem;
    margin: 0;
}

#Display .main-container .tabs-container .list-icon svg {
    margin-right: 10px;
}

/*#Display .main-container .tabs-container {*/
/*    margin-top: 30px;*/
/*}*/

#Display .main-container .tabs-container h2 {
    color: var(--blue);
    font-size: 1.5em;
    text-indent: 0;
    margin: 0;
}

#Display .pagination {
    margin-top: 20px;
}

#Display .relations-tab .relation-name {
    margin-top: 20px !important;
}

#Display .franchise-tab {
    text-align: center;
}

#Display .server-tab .btn-group button, #Display .server-tab .btn-group a {
    margin-right: 10px;
}

#Display .server-tab .edit-btn {
    display: inline-block;
}