#Private-Page {
    position: relative;
    height: 100vh;
    background-image: url("../images/girl_phone_window.jpg");
    background-size: cover;
    background-position: center;
    overflow: hidden;
    text-align: center;
}
#Private-Page .container {
    position: absolute;
    top: 50%;
    height: 100px;
    margin-top: -50px;
    user-select: none;
}
#Private-Page .container h1 {
    font-size: 5rem;
}
#Private-Page .container h1,
#Private-Page .container p {
    margin: 0;
}