/*NAVBAR*/
#Cyber-Navbar {
    position: fixed;
    width: 100%;
    top: 0;
    background-color: transparent;
    transition: .2s;
}
#Cyber-Navbar header {
    box-shadow: none !important;
}

.Cyber-Navbar-scrolled {
    background-color: var(--darker-blue-rgba-095) !important;
}

#Cyber-Navbar a {
    color: #ffffff;
    transition: .1s;
}
#Cyber-Navbar a:hover {
    color: var(--blue);
}

#Cyber-Navbar span:hover {
    cursor: pointer;
}

#Cyber-Navbar .desktop-menu {
    position: absolute !important;
    background-color: var(--black-02);
    backdrop-filter: blur(2px);top: 2em;
    z-index: 900;
    opacity: 0;
    transition: 0.2s;
    border-right: 5px;
}

#Cyber-Navbar .desktop-menu-reversed {
    margin-right: 15px;
    right: 0;
}

#Cyber-Navbar .show-dropdown {
    transform: scaleY(1);
    opacity: 1;
    transform-origin: top;
    transition: 0.2s;
}

#Cyber-Navbar .desktop-menu .menu-link {
    text-decoration: none;
    display: block;
    padding: 2px 10px;
    transition: .2s;
}

#Cyber-Navbar .desktop-menu .menu-link:first-child {
    padding-top: 10px;
}

#Cyber-Navbar .desktop-menu .menu-link:last-child {
    padding-bottom: 10px;
}
/*NAVBAR*/


/*FOOTER*/
#Footer .react-parallax .react-parallax-bgimage {
    filter: brightness(0.5) !important;
}

#Footer .react-parallax-content {
    filter: brightness(1) !important;
    padding: 200px 0;
}

#Footer .logos-container {
    text-align: center;
}

#Footer .logo-container {
    padding: 0 50px;
    display: inline-block;
}

#Footer .logo-container-side h5 {
    font-size: 1.25rem;
    font-variant: small-caps;
}

#Footer .logo-container-side img {
    width: 100px;
}

#Footer .logo-container-main h3 {
    font-size: 1.75rem;
    font-variant: small-caps;
}

#Footer .logo-container-main img {
    width: 150px;
}
/*FOOTER*/


/*ALERTS*/
#Alerts {
    height: 100vh;
    width: 100%;
    position: fixed;
    top: 0;
    pointer-events: none;
    animation-name: fade-in;
    animation-duration: 2s;
    animation-fill-mode: forwards;
    opacity: 0;
    transition: .2s;
}

#Alerts .alert {
    position: fixed;
    bottom: 1rem;
    pointer-events: initial;
    margin: 0 1rem;
    min-width: 300px;
    max-width: 50%;
}

#Alerts .alert .MuiAlert-action {
    align-items: baseline;
}

@keyframes fade-in {
    from {
        opacity: 0;
    }
    to {
        opacity: 1;
    }
}
/*ALERTS*/

/*CARDS*/
.title-card {
    position: absolute;
    width: 100%;
    height: 100%;
    background-size: cover;
    background-position: center;
    color: #ffffff;
    pointer-events: none;
    opacity: 0;
    font-weight: bold;
    text-shadow: 2px 2px 3px #000000;
    transition: .1s;
}

.title-card:hover {
    cursor: pointer;
    transform: scale(1.04);
    color: #ffffff;
}

.title-card .chip-array {
    position: absolute;
    margin-top: 5px;
    margin-left: 5px;
}

.title-card .chip-array .MuiChip-label {
    font-size: .85em;
    padding: 2px 5px;
}

.title-card .chip-array .MuiChip-root {
    background-color: var(--black-03);
    margin: 0 2px;
    height: fit-content;
}

.title-card .title-card-body {
    position: absolute;
    bottom: 0;
    width: 100%;
    padding-bottom: 5px;
}

.title-card .title-card-body .chip-array {
    width: 100%;
    text-align: center;
    position: relative;
}

.title-card .title-card-body .chip-array .MuiChip-colorPrimary {
    margin: 0 2px;
}

.title-card h3 {
    text-align: center;
    margin-top: 35px;
}

.title-card h4 {
    text-align: center;
    margin-top: 10px;
}

.title-card-container .user-buttons {
    position: absolute;
    z-index: 1;
    width: 100%;
    text-align: right;
    opacity: 0;
    transition: .2s;
    user-select: none !important;
    padding: 0;
    pointer-events: none !important;
    animation-name: FadeInUserButtons;
    animation-fill-mode: forwards;
    animation-duration: .3s;
}

.title-card-container .user-buttons-hidden {
    position: absolute;
    z-index: 1;
    width: 100%;
    text-align: right;
    opacity: 0 !important;
    transition: .2s;
    user-select: none !important;
    padding: 0;
    pointer-events: none !important;
}

.title-card-mini {

}

@keyframes FadeInUserButtons {
    from {
        opacity: 0;
        padding: 0;
    }
    to {
        opacity: 1;
        padding: 5px 2px 0 0;
    }
}

.title-card-container .user-buttons .add-button {
    min-width: 10px !important;
    min-height: 30px !important;
    user-select: initial !important;
    pointer-events: initial !important;
    margin: 0 3px;
}

.title-card-container .user-buttons .drop-button {
    min-width: 10px !important;
    min-height: 30px !important;
    user-select: initial !important;
    pointer-events: initial !important;
    margin: 0 3px;
}

.title-card-container .user-buttons .user-buttons-loading {
    margin-right: 3px;
}

/*CARDS*/


/*PAGINATION*/
.pagination {
    position: sticky;
    bottom: 0;
    width: 100%;
    pointer-events: none;
    z-index: 1000;
}

.pagination .MuiPagination-root {
    width: fit-content;
    margin: 0 auto;
    background-color: var(--darker-blue-rgba-095);
    border-radius: 17px;
    padding: 3px;
    pointer-events: initial;
}

.go-to-page-dialog {
    padding: 0 20px 20px;
    text-align: center;
}

.go-to-page-dialog .MuiInputLabel-formControl {
    color: #9e9e9e;
}

.go-to-page-dialog button {
    display: block;
    margin: 20px auto 0;
}
/*PAGINATION*/


/*LOADING*/
.triangle-loader {
    position: relative;
    height: 100vh;
    width: 100%;
    overflow: hidden;
    color: rgba(255, 255, 255, 0.8);
}

.triangle-loader-initial {
    position: initial !important;
    height: auto !important;
    width: auto !important;
}

.triangle-loader .triangle-loader-svg {
    position: absolute;
    width: min-content;
    margin: 0;
    top: 50%;
    left: 50%;
    text-align: center;
    -ms-transform: translate(-50%, -50%);
    transform: translate(-50%, -50%);
}

.triangle-loader-initial .triangle-loader-svg {
    position: initial !important;
    margin: 0 auto !important;
    transform: none !important;
}

.triangle-loader .triangle-loader-svg svg {
    -webkit-transform-origin: 50% 65%;
    transform-origin: 50% 65%;
}

.triangle-loader .triangle-loader-svg svg polygon {
    stroke-dasharray: 17;
    -webkit-animation: dash 2.5s cubic-bezier(0.35, 0.04, 0.63, 0.95) infinite;
    animation: dash 2.5s cubic-bezier(0.35, 0.04, 0.63, 0.95) infinite;
}

@-webkit-keyframes dash {
    to {
        stroke-dashoffset: 136;
    }
}

@keyframes dash {
    to {
        stroke-dashoffset: 136;
    }
}

@-webkit-keyframes rotate {
    100% {
        -webkit-transform: rotate(360deg);
        transform: rotate(360deg);
    }
}

@keyframes rotate {
    100% {
        -webkit-transform: rotate(360deg);
        transform: rotate(360deg);
    }
}
/*LOADING*/


