/*CREATE MATERIAL UI THEME: https://in-your-saas.github.io/material-ui-theme-editor/*/
a {
    color: var(--blue);
    transition: .2s;
    text-decoration: none;
}

a:hover {
    color: var(--dark-blue);
    transition: .2s;
}


/*INITIAL LOADING*/
#Initial-Loading {
  display: flex;
  justify-content: center;
  align-items: center;
  height: 100vh;
}

#Initial-Loading .loading-container {
    text-align: center;
}

#Initial-Loading .loading {
    width: 7rem !important;
    height: 7rem !important;
}

#Initial-Loading .loading-container .text {
    color: var(--blue);
}
/*INITIAL LOADING*/


/*REACT ALERT*/
#__react-alert__ .alert {
    margin: 3px 3px 5px 5px;
}

#__react-alert__ .alert .MuiAlert-action {
    pointer-events: initial !important;
}

/*REACT ALERT*/


/*ALIGNS*/
.text-center {
    text-align: center;
}
.text-left {
    text-align: left;
}
.text-right {
    text-align: right;
}
.text-justify {
    text-align: justify;
}
/*ALIGNS*/


/*TYPOGRAPHY*/
