}

.color-secondary-light {
    color: var(--light-magenta) !important;
}

.color-info {
    color: var(--cyan) !important;
}

.color-success {
    color: var(--green) !important;
}

.color-warning {
    color: var(--yellow) !important;
}

.color-danger {
