/*INPUTS*/
.btn-contained-default {
    color: #fff !important;
    background-color: var(--gray) !important;
}
.btn-default {
    border-color: var(--gray) !important;
    color: var(--gray) !important;
}
.btn-contained-default:hover,
.btn-default:hover {
    background-color: rgba(123, 123, 123, 0.2) !important;
}

.btn-contained-primary, .btn-contained-primary-dh {
    color: #fff !important;
    background-color: var(--blue) !important;
}
.btn-primary {
    border-color: var(--blue) !important;
    color: var(--blue) !important;
}
.btn-contained-primary:hover,
.btn-primary:hover {
    background-color: rgba(27, 107, 183, 0.2) !important;
}
.btn-contained-primary-dh:hover {
    background-color: rgba(14, 75, 128, 0.9) !important;
}

.btn-contained-secondary {
    color: #fff !important;
    background-color: var(--magenta) !important;
}
.btn-secondary {
    border-color: var(--magenta) !important;
    color: var(--magenta) !important;
}
.btn-contained-secondary:hover,
.btn-primary:hover {
    background-color: rgba(255, 78, 202, 0.2) !important;
}

.btn-contained-info {
    color: #fff !important;
    background-color: var(--dark-cyan) !important;
}
.btn-info {
    border-color: var(--dark-cyan) !important;
    color: var(--dark-cyan) !important;
}
.btn-contained-info:hover,
.btn-info:hover {
    background-color: rgba(0, 255, 255, 0.2) !important;
}

.btn-contained-success {
    color: #fff !important;
    background-color: var(--green) !important;
}
.btn-success {
    border-color: var(--green) !important;
    color: var(--green) !important;
}
.btn-contained-success:hover,
.btn-success:hover {
    background-color: rgba(0, 160, 87, 0.2) !important;
}

.btn-contained-warning {
    color: #fff !important;
    background-color: var(--yellow) !important;
}
.btn-warning {
    border-color: var(--yellow) !important;
    color: var(--yellow) !important;
}
.btn-contained-warning:hover,
.btn-warning:hover {
    background-color: rgba(208, 156, 0, 0.2) !important;
}

.btn-contained-danger, .btn-contained-danger-dh {
    color: #fff !important;
    background-color: var(--red) !important;
}
.btn-danger {
    border-color: var(--red) !important;
    color: var(--red) !important;
}
.btn-contained-danger:hover,
.btn-danger:hover {
    background-color: rgba(255, 0, 0, 0.2) !important;
}
.btn-contained-danger-dh:hover {
    background-color: rgba(195, 0, 0, 0.9) !important;
}

.form-control .MuiInputLabel-formControl, .form-control .MuiInputLabel-formControl {
    color: #9e9e9e;
}
/*INPUTS*/


/*BOXES*/
.box-light {
    background-color: var(--black-01);
    border-radius: 5px;
    padding: 10px;
}

/*BOXES*/


/*TABS*/
.cyber-tabs {
    background-color: var(--black-01) !important;
}

.cyber-tabs button {
    transition: .1s;
}

.cyber-tabs button:hover {
    background-color: var(--black-01) !important;
}

.MuiTabs-indicator {
    background-color: var(--blue) !important;
}

.Mui-disabled {
    color: gray !important;
}
/*TABS*/


/*IMAGES*/
.lazy-loaded-img {
    position: absolute !important;
    opacity: 0 !important;
    width: 0 !important;
    height: 0 !important;
    margin: 0 !important;
    padding: 0 !important;
    user-select: none !important;
    pointer-events: none !important;
}

.before-fade-in {
    opacity: 0;
}

.fade-in {
    opacity: 0;
    pointer-events: initial;
    animation-name: fade-in;
    animation-duration: 1s;
    animation-fill-mode: forwards;
}
/*IMAGES*/


/*CHIP*/
.chip-default {
    color: #fff;
    background-color: var(--gray) !important;
}

.chip-primary {
    color: #fff;
    background-color: var(--blue) !important;
}

.chip-secondary {
    color: #fff;
    background-color: var(--magenta) !important;
}

.chip-success {
    color: #fff;
    background-color: var(--green) !important;
}

.chip-info {
    color: #fff;
    background-color: var(--darker-cyan) !important;
}

.chip-warning {
    color: #fff;
    background-color: var(--yellow) !important;
}

.chip-danger {
    color: #fff;
    background-color: var(--red) !important;
}
/*CHIP*/


/*COLOR PICKER MODULE*/
#color-popover label, #color-popover p {
    color: var(--gray)
}
/*COLOR PICKER MODULE*/


/*MISC*/
.copy-to-clipboard-text:hover {
    cursor: pointer;
}

/*MISC*/


/* SCROLL BAR */
::-webkit-scrollbar {
    width: 6px;
    height: 6px;
    background-color: var(--darkest-blue);
}

::-webkit-scrollbar-track {
    -webkit-box-shadow: inset 0 0 6px var(--darkest-blue);
    background-color: var(--darkest-blue);
}

::-webkit-scrollbar-corner {
    -webkit-box-shadow: inset 0 0 6px var(--darkest-blue);
    background-color: var(--darkest-blue);
}

::-webkit-scrollbar-thumb {
    /*background-color: #156bb7;*/
    background-color: var(--dark-blue);
}

::-webkit-scrollbar-thumb:hover {
    /*background-color: #344675;*/
    background-color: var(--blue);
}
/* SCROLL BAR */
