html {
    color: #ffffff;
    height: 100vh;
    max-width: 100%;
}

body {
    margin: 0;
    min-height: 100vh;
    font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
    -webkit-font-smoothing: antialiased;
    -moz-osx-font-smoothing: grayscale;
    background-image: url("assets/images/dots.png");
    background-size: 1650px;
    background-repeat: repeat;
    background-color: #171941;
}

code {
    font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

p, pre, a, span, h1, h2, h3, h4, h5, h6, td, tr {
    font-family: 'Quicksand', sans-serif !important;
}

h1, h2, h3, h4, h5, h6 {
    font-family: 'Quicksand', sans-serif !important;
    font-weight: 500;
}

:root {
    --black-05: rgba(0, 0, 0, 0.5);
    --black-04: rgba(0, 0, 0, 0.4);
    --black-03: rgba(0, 0, 0, 0.3);
    --black-02: rgba(0, 0, 0, 0.2);
    --black-01: rgba(0, 0, 0, 0.1);
    --gray: #7b7b7b;
    --darkest-blue: #171941;
    --darker-blue: #1f2251;
    --darker-blue-rgba-095: rgba(31, 34, 81, 0.95);
    --darkish-blue: #2b3553;
    --dark-blue: #133E7C;
    --dark-blue-rgba-095: rgba(19, 62, 124, 0.95);
    --blue: #156bb7;
    --cyan: #00ffff;
    --dark-cyan: #00a0a0;
    --darker-cyan: #003838;
    --darker-cyan-035: rgba(0, 56, 56, 0.35);
    --green: #00a057;
    --yellow: #d09c00;
    --light-magenta: #dc69ca;
    --magenta: #e14eca;
    --dark-magenta: #8b3379;
    --darker-magenta: #280028;
    --darker-magenta-035: rgba(40, 0, 40, 0.35);
    --darkest-red: #460000;
    --darker-red: #790000;
    --dark-red: #ff0000;
    --red: #ff0000;
}
