#User-Title-Entry {
    display: inline-block;
}

#User-Title-Entry-Form .MuiFormLabel-root {
    color: var(--gray);
}

#User-Title-Entry-Form .form-group {
    padding: 10px 0;
}

#User-Title-Entry-Form .form-group .MuiGrid-item {
    padding: 0 5px;
}

#User-Title-Entry-Form .form-control {
    width: 100%;
}

#User-Title-Entry-Form button {
    font-size: 1rem;
}

#User-Title-Entry-Form .MuiDialogActions-root {
    margin-top: 15px;
}


/*HISTORY MODAL*/
#History-Modal {
    display: inline-block;
}
#History-Modal-Dialog .header-title {
    color: var(--gray);
}

#History-Modal-Dialog .delete-btn:hover {
    cursor: pointer;
    color: var(--darker-red);
}

#History-Modal-Dialog .delete-btn {
    color: var(--red);
    margin-left: 10px;
    transition: .1s;
}
/*HISTORY MODAL*/
