/*LOGIN*/
#Login, #Register {
    height: 100vh;
    background-image: url("../images/girl-window.jpg");
    background-size: cover;
    background-position: center;
    overflow: hidden;
    opacity: 0;
}

.login-img-loaded {
    opacity: 0;
    animation-name: fade-in;
    animation-duration: 1s;
    animation-fill-mode: forwards;
}

#Login .form-container h1, #Register .form-container h1 {
    text-align: center;
}

#Login .form-container, #Register .form-container {
    animation-name: fade-in;
    animation-duration: 2s;
    animation-fill-mode: forwards;
    opacity: 0;
    transition: .2s;
}

#Login .form-container, #Register .form-container {
    width: 350px;
    position: absolute;
    top: 50%;
    left: 50%;
    -ms-transform: translate(-50%, -50%);
    transform: translate(-50%, -50%);
}

#Login #Login-Form .form-control, #Register #Register-Form .form-control {
    display: block;
    margin: 15px 0;
}

#Login #Login-Form .form-control, #Register #Register-Form .form-control {
    display: block;
    margin: 15px 0;
}

#Login #Login-Form .form-control .MuiInput-formControl, #Register #Register-Form .form-control .MuiInput-formControl {
    width: 100%;
}

#Login #Login-Form button, #Register #Register-Form button {
    display: block;
    margin: 15px auto 0;
}

#Login .loading, #Register .loading {
    text-align: center;
    padding: 70px 0;
}

#Login .loading div, #Register .loading div {
    width: 80px !important;
    height: 80px !important;
}
/*LOGIN*/
