/*TABS*/
#Display .main-container .tabs-container .general-description h2:first-child {
    margin: 0 0 15px;
}
#Display .main-container .tabs-container .general-description h2 {
    margin: 15px 0;
}
#Display .main-container .tabs-container .general-description p {
    margin: 5px 0;
    text-indent: 1em;
}
#Display .main-container .tabs-container .characters-tab {
    padding: 0;
}
#Display .main-container .tabs-container .server-tab table td:first-child {
    color: var(--magenta);
}
#Display .main-container .tabs-container .server-tab .lang-chipset div {
    border-color: var(--magenta);
    margin: 0 3px;
}
#Display .main-container .tabs-container .server-tab .lang-chipset div span {
    color: var(--magenta);
    text-transform: uppercase;
}
#Display .main-container .tabs-container .general-tab .image-gallery div:first-child {
    border-radius: 5px;
}
#Display .main-container .tabs-container .general-tab .image-gallery .image-gallery-thumbnail:hover {
    cursor: pointer;
}
/*TABS*/

/*STATISTICS*/
#Anime-Statistics {
    width: 100%;
    /*background-color: var(--black-04);*/
    border-radius: 5px;
    padding-top: 15px;
    padding-bottom: 15px;
}

#Anime-Statistics h3 {
    text-align: center;
    margin-bottom: 20px;
}

#Anime-Statistics p {
    font-size: 1.25rem;
}

#Anime-Statistics .charts-divider,
#Anime-Home .charts-divider {
    margin: 50px;
}

#Anime-Statistics .pie-container {
    width: 300px;
    height: 350px;
    margin: 0 auto;
}

#Anime-Statistics .bar-container {
    width: 100%;
    height: 500px;
    margin: 0 auto 50px;;
}

#Anime-Statistics table {
    margin: 0 auto;
}

#Anime-Statistics table p {
    padding-left: 10px;
    margin: 0 !important;
}
/*STATISTICS*/
