#Seasons {
    padding-top: 60px;
}

#Seasons .season-card {
    padding: 10px !important;
}

.spring {
    color: #00ff00;
}

.summer {
    color: #ffff00;
}

.fall {
    color: #e25e00;
}

#Seasons .season-card h1 {
    text-align: center;
    margin: 11px 0;
}

#Seasons .season-card .label {
    text-align: right;
    white-space: nowrap;
}

#Seasons .season-card .label svg {
    margin-right: 10px;
}

#Seasons .season-card .label {
    font-size: 1.4rem;
}

#Seasons .season-card .chip div {
    margin-left: 10px;
    font-size: 1.2rem;
    vertical-align: -8px;
}

#Seasons .season-card .total {
    text-align: center;
    font-size: 1.2rem;
}

/*DISPLAY SEASON*/
#Seasons .title-segment {
    margin-bottom: -50px;
    text-align: center;
}

#Seasons .title-segment button {
    margin: 0 15px;
}

#Seasons .title-segment h1 {
    display: inline-block;
    margin: 0;
    vertical-align: middle;
}
/*DISPLAY SEASON*/
