/*HOME PAGE*/
#Home {
    padding-top: 60px;
}

#Home .bg-particles {
    position: fixed;
    top: 0;
    height: 100vh;
    width: 100%;
}

#Home h1 {
    text-align: center;
}

#Home .user-card-container {
    width: 100%;
}

#Home .user-card-container .MuiGridListTile-tile {
    z-index: 1;
}

#Home .user-card {
    display: block;
    width: 100%;
    height: 100%;
    color: #fff;
    transition: .1s;
}

#Home .user-card:hover {
    transform: scale(1.04);
    cursor: pointer;
}

#Home .user-card img {
    width: 100%;
    height: 100%;
    object-fit: cover;
}

#Home .user-card .card-text {
    position: absolute;
    width: 100%;
    text-align: center;
    height: 100%;
    z-index: 1000;
}

#Home .user-card .card-text h2 {
    text-shadow: 2px 2px 3px #000000;
}
/*HOME PAGE*/


/*WELCOME INDEX STYLE*/
#Index {
    overflow-x: hidden;
    width: 100%;
}

#Index .welcome-img-container {
    height: 100vh;
    user-select: none;
}

#Index .text-right {
    text-align: right;
}

#Index .welcome-img-container #Index-Video {
    width: 100vw;
    height: 100vh;
    object-fit: cover;
}

#Index .welcome-img-container img {
    /*height: 100vh;*/
    width: 100%;
    opacity: 0;
    object-fit: cover;
    animation-name: fade-in;
    animation-duration: 2s;
    animation-fill-mode: forwards;
    transition: .2s;
}

#Index .welcome-img-container .title-container {
    text-align: center;
    position: absolute;
    width: 100%;
    top: 25vh;
    opacity: 0;
    animation-name: fade-in, move-down;
    animation-duration: 2s, 2s;
    animation-delay: 1s, 1s;
    animation-fill-mode: forwards, forwards;
    transition: .2s;
    margin-top: -30px;
    z-index: 1;
}

#Index .welcome-img-container h1 {
    font-size: 5rem;
    line-height: 0.8;
}

#Index .welcome-img-container h5 {
    font-size: 1.25rem;
}

#Index .welcome-img-container h1, #Index .welcome-img-container h5 {
    font-weight: normal;
    font-variant: small-caps;
    margin: 0;
}

#Index .home-login-link-container {
    width: 100%;
    text-align: center;
    position: absolute;
    bottom: 5vh;
}

#Index .home-login-link-container a {
    font-weight: bold;
}

@keyframes move-down {
    from {
        margin-top: -30px;
    }
    to {
        margin-top: 0;
    }
}

#Index {
}

#Index .project-about img {
    vertical-align: middle;
}

#Index .project-about .project-about-wrapper {
    padding: 0;
}

#Index .project-about-wrapper .react-parallax {
    opacity: 0;
}

#Index .react-parallax .react-parallax-bgimage {
    filter: brightness(0.5) !important;
}

#Index .project-about-wrapper .react-parallax-content {
    filter: brightness(1) !important;
    padding: 75px 0;
    text-shadow: 2px 2px 2px black;
}

#Index .project-about .img-content-container {
    padding: 0 3%
}

#Index .project-about h1 {
    display: inline-block;
    font-variant: small-caps;
    margin: 0;
}

#Index .project-about span {
    font-size: 1.5rem;
    font-variant: small-caps;
    font-weight: bold;
    color: var(--dark-blue);
}

#Index .project-about p {
    max-width: 35rem;
    text-indent: 1rem;
    font-size: 1.1rem;
}

#Index .project-about .about-container {
    display: inline-block;
    vertical-align: middle;
    text-align: left;
}

#Index .project-about img {
    height: 300px;
}

#Index .project-about .cyber-req-logo-top {
    display: none;
}

#Index .project-about .cyber-req-logo-bottom {
    display: inline-block;
}

@media only screen and (max-width: 600px) {
    #Index .project-about img {
        height: 130px;
    }
}

@media only screen and (max-width: 920px) {
    #Index .project-about .project-about-wrapper,
    #Index .project-about .about-container {
        text-align: center !important;
    }

    #Index .text-right {
        text-align: center;
    }

    #Index .project-about .cyber-req-logo-top {
        display: inline-block;
    }

    #Index .project-about .cyber-req-logo-bottom {
        display: none;
    }
}

#Index .home-info {
    margin-top: 100px;
}

#Index .home-info h1 {
    margin-bottom: 50px;
}
/*WELCOME INDEX STYLE*/


/* ANIME HOME & MANGA HOME */
#Anime-Home, #Manga-Home, #Characters-Home, #Eroge-Home {
    margin-top: 60px;
}

.title-home-page .top-titles-tabs {
    width: 100%;
}

.title-home-page .app-bar {
    background-color: var(--black-01) !important;
}

.title-home-page .top-titles-tabs .top-titles-tab .MuiBox-root {
    padding: 0;
}

#Current-Season,
.title-home-page .horizontal-titles {
    margin-bottom: 20px;
}

#Current-Season .current-season-header,
.title-home-page .horizontal-titles .horizontal-titles-header {
    text-align: center;
}

#Current-Season .current-season-header h3,
.title-home-page .horizontal-titles .horizontal-titles-header h3 {
    padding: 0 15px;
}

#Current-Season .current-season-titles-container,
.title-home-page .horizontal-titles .horizontal-titles-container {
    height: 440px;
    overflow-y: hidden;
    overflow-x: auto;
}

#Current-Season .current-season-titles-container .current-season-titles-wrapper,
.title-home-page .horizontal-titles .horizontal-titles-container .horizontal-titles-wrapper {
    width: max-content;
}

#Current-Season .current-season-card-container,
.title-home-page .horizontal-titles .horizontal-titles-card-container {
    display: inline-block;
    width: 300px;
    height: 430px;
    position: relative;
    overflow: hidden;
}

#recent-history-container h3 {
    text-align: center;
}
/*ANIME HOME & MANGA HOME*/
