/* Variables */
/* Fonts */
body {
    font-size: 1em;
    font-weight: 300;
    line-height: 1.5;
    letter-spacing: 0.05em;
}

/* Layout */
* {
    -moz-box-sizing: border-box;
    -webkit-box-sizing: border-box;
    box-sizing: border-box;
}

/* Styling */
.timeline {
    margin: 4em auto;
    position: relative;
    /*max-width: 46em;*/
    width: 100%;
}

.timeline:before {
    background-color: var(--darker-cyan);
    content: '';
    margin-left: -1px;
    position: absolute;
    top: 0;
    left: 4.5em;
    width: 2px;
    height: 100%;
}

.timeline-event {
    position: relative;
    list-style: none;
}

.timeline-event:hover .timeline-event-icon {
    -moz-transform: rotate(-45deg);
    -ms-transform: rotate(-45deg);
    -webkit-transform: rotate(-45deg);
    transform: rotate(-45deg);
    background-color: var(--dark-cyan);
}

.timeline-event:hover .timeline-event-thumbnail {
    -moz-box-shadow: inset 40em 0 0 0 var(--dark-cyan);
    -webkit-box-shadow: inset 40em 0 0 0 var(--darker-cyan);
    box-shadow: inset 40em 0 0 0 var(--darker-cyan);
}

.timeline-event-copy {
    padding: 2em;
    position: relative;
    top: -1.875em;
    left: 4em;
    width: 80%;
}

/*.timeline-event-copy h3 {*/
/*    font-size: 1.75em;*/
/*}*/

.timeline-event-copy h4 {
    font-size: 1.2em;
    margin-bottom: 1.2em;
}

.timeline-event-copy strong {
    font-weight: 700;
}

.timeline-event-copy p:not(.timeline-event-thumbnail) {
    padding-bottom: 1.2em;
}

.timeline-event-icon {
    -moz-transition: -moz-transform 0.2s ease-in;
    -o-transition: -o-transform 0.2s ease-in;
    -webkit-transition: -webkit-transform 0.2s ease-in;
    transition: transform 0.2s ease-in;
    -moz-transform: rotate(45deg);
    -ms-transform: rotate(45deg);
    -webkit-transform: rotate(45deg);
    transform: rotate(45deg);
    background-color: black;
    outline: 10px solid white;
    display: block;
    margin: 0.5em 0.5em 0.5em -0.5em;
    position: absolute;
    top: 0;
    left: 2em;
    width: 1em;
    height: 1em;
}

.timeline-event-thumbnail {
    -moz-transition: box-shadow 0.5s ease-in 0.1s;
    -o-transition: box-shadow 0.5s ease-in 0.1s;
    -webkit-transition: box-shadow 0.5s ease-in;
    -webkit-transition-delay: 0.1s;
    transition: box-shadow 0.5s ease-in 0.1s;
    color: white;
    font-size: 0.75em;
    background-color: black;
    -moz-box-shadow: inset 0 0 0 0 var(--dark-cyan);
    -webkit-box-shadow: inset 0 0 0 0 var(--dark-cyan);
    box-shadow: inset 0 0 0 0 var(--dark-cyan);
    display: inline-block;
    margin-bottom: 1.2em;
    padding: 0.25em 1em 0.2em 1em;
}

.timeline-history-segment {
    display: inline-block;
    vertical-align: top;
}

.timeline-history-segment-episodes {
    padding: 10px 0 0 30px;
}

.timeline-history-segment-episodes p {
    padding-bottom: .6em !important;
}

