.download-torrent-dialog .MuiPaper-root, .edit-anime-on-server-dialog .MuiPaper-root {
    background-color: var(--darker-magenta);
}

.content-container #disk, #Edit-Anime-On-Server-Form .input-label {
    color: var(--gray) !important;
}

#form-dialog-title {
    width: 600px;
    padding-bottom: 0;
}

#Download-Anime-Torrent-Form .form-group {
    margin: 15px 0;
}

#Download-Anime-Torrent-Form .form-control, #Edit-Anime-On-Server-Form .form-control {
    width: 100%;
}

#Download-Anime-Torrent-Form .extract-subs-container {
    padding-left: 20px;
}

#Download-Anime-Torrent-Form .torrent-form-group {
    margin-top: 30px;
}

#Download-Anime-Torrent-Form .torrent-label-container {
    padding-top: 30px;
}

#Download-Anime-Torrent-Form .extra-buttons-containers {
    text-align: center;
    padding-top: 10px;
    font-size: 1.5rem;
}

#Download-Anime-Torrent-Form .extra-buttons-containers .selected {
    color: var(--green);
}

#Download-Anime-Torrent-Form #extra-btn, #Download-Anime-Torrent-Form #trash-btn {
    transition: .1s;
}

#Download-Anime-Torrent-Form #extra-btn:hover, #Download-Anime-Torrent-Form #trash-btn:hover {
    cursor: pointer;
    opacity: .6;
}

#Download-Anime-Torrent-Form #trash-btn {
    color: var(--red);
}

#Download-Anime-Torrent-Form .dropzone p {
    color: var(--gray);
    border: 1px solid var(--gray);
    padding: 10px 20px;
    border-radius: 5px;
    margin: 0;
    height: 70px;
    transition: .2s;
}

#Download-Anime-Torrent-Form .dropzone p:hover {
    border: 1px solid var(--dark-magenta);
    cursor: pointer;
}

#Download-Anime-Torrent-Form .dropzone .hovered {
    border: 1px solid var(--dark-magenta);
}

#Download-Anime-Torrent-Form .submit-container {
    margin-top: 30px;
    margin-bottom: 10px;
    text-align: right;
}

#Edit-Anime-On-Server-Form .form-group {
    margin: 30px 0;
}

#Edit-Anime-On-Server-Form .form-group:first-child {
    margin-top: 0;
}

#Edit-Anime-On-Server-Form .language-box {
    display: inline-block;
}

#Edit-Anime-On-Server-Form .expand-btn {
    margin-left: 10px;
}

#menu-disk .MuiPaper-root,
#menu-video_quality .MuiPaper-root ,
#menu-audio_channel .MuiPaper-root {
    background-color: var(--dark-magenta);
}

.add-franchises-container {
    display: inline-block;
}

.add-franchises-container .add-to-extra-ids-btn {
    position: absolute;
    z-index: 2;
    margin-top: 165px;
    margin-left: 10px;
    font-size: 1.3em;
    opacity: .8;
    transition: .2s;
    color: var(--magenta);
}

.added-franchise {
    /*background-color: #00a057;*/
    opacity: 0.3 !important;
    animation-name: glow;
    animation-duration: 3s;
    animation-iteration-count: infinite;
}

.add-franchises-container {
    height: 212px;
}

.add-franchises-container .add-to-extra-ids-btn:hover {
    cursor: pointer;
    opacity: 1;
}

@keyframes glow {
    0% {
        background-color: #860086;
    }
    50% {
        background-color: #ff94fc;
    }
    100% {
        background-color: #860086;
    }
}

.torrent-info-container .torrent-info {
    margin: 0 10px;
}
