/* ************* FILTERS ************ */
#Filters {
    margin: 15px 0;
    font-family: 'Quicksand', sans-serif;
}

#Filters h6 {
    margin: 20px 0 10px;
    font-size: .8rem;
    font-weight: bold;
    text-transform: uppercase;
}

#Filters .total-results {
    display: inline-block;
    margin-right: 10px;
}

#Filters .search-field-container {
    text-align: center;
}

#Filters .search-field {
    width: 90%;
}

#Filters .active-filters-wrapper {
    display: inline-block;
}

#Filters .active-filters-wrapper div {
    margin: 3px;
}

#Filters .input-group label {
    border: 1px solid #2b3553;
    background-color: transparent;
    border-radius: 5px;
    padding: 3px 8px;
    line-height: 1.5;
    transition: .2s;
    -webkit-user-select: none;
    user-select: none;
    font-size: .8rem;
}

#Filters .filter-group {
    text-align: center;
}

#Filters .input-group {
    display: inline-block;
    margin: 3px 3px;
}

/*#Filters .active-filters-wrapper .chip-primary,*/
#Filters .filters-form input[type="checkbox"]:checked+label,
#Filters .filters-form input[type="checkbox"]:checked+.label-primary,
#Filters .filters-form input[type="radio"]:checked+label,
#Filters .filters-form input[type="radio"]:checked+.label-primary {
    border: 1px solid var(--blue);
    color: var(--blue);
}

/*#Filters .active-filters-wrapper .chip-default,*/
#Filters .filters-form input[type="checkbox"]:checked+.label-default,
#Filters .filters-form input[type="radio"]:checked+.label-default {
    border: 1px solid var(--darker-blue) !important;
    color: var(--darker-blue);
}

/*#Filters .active-filters-wrapper .chip-secondary,*/
#Filters .filters-form input[type="checkbox"]:checked+.label-secondary,
#Filters .filters-form input[type="radio"]:checked+.label-secondary {
    border: 1px solid var(--magenta) !important;
    color: var(--magenta);
}

/*#Filters .active-filters-wrapper .chip-warning,*/
#Filters .filters-form input[type="checkbox"]:checked+.label-warning,
#Filters .filters-form input[type="radio"]:checked+.label-warning {
    border: 1px solid var(--yellow) !important;
    color: var(--yellow);
}

/*#Filters .active-filters-wrapper .chip-info,*/
#Filters .filters-form input[type="checkbox"]:checked+.label-info,
#Filters .filters-form input[type="radio"]:checked+.label-info {
    border: 1px solid var(--dark-cyan) !important;
    color: var(--dark-cyan);
}

/*#Filters .active-filters-wrapper .chip-danger,*/
#Filters .filters-form input[type="checkbox"]:checked+.label-danger,
#Filters .filters-form input[type="radio"]:checked+.label-danger {
    border: 1px solid var(--red) !important;
    color: var(--red);
}

#Filters label:hover, #Filters .label-primary:hover {
    border: 1px solid var(--blue);
    color: var(--blue) !important;
    cursor: pointer;
}

#Filters .label-default:hover {
    border: 1px solid var(--darker-blue);
    color: #fff;
    cursor: pointer;
}

#Filters .label-secondary:hover {
    border: 1px solid var(--magenta);
    color: var(--magenta) !important;
    cursor: pointer;
}

#Filters .label-warning:hover {
    border: 1px solid var(--yellow);
    color: var(--yellow) !important;
    cursor: pointer;
}

#Filters .label-info:hover {
    border: 1px solid var(--dark-cyan);
    color: var(--dark-cyan) !important;
    cursor: pointer;
}

#Filters .label-danger:hover {
    border: 1px solid var(--red);
    color: var(--red) !important;
    cursor: pointer;
}

#Filters .slider-group {
    width: 90%;
}

#Filters .slider-group .MuiSlider-markLabel {
    color: var(--gray);
}

#Filters .slider-group .MuiSlider-markLabelActive {
    color: #fff;
}

#order-filters span {
    border-top: 1px solid #2b3553;
    border-bottom: 1px solid #2b3553;
    display: inline-block;
    font-size: .8rem;
    height: 27px;
    padding: 3px 6px 0 6px;
}

#Filters .genre-container {
    display: inline-block;
    height: 70px;
    max-width: 200px;
    width: 150px;
    background-size: cover;
    position: relative;
    overflow: hidden;
    margin: 0 0 -6px;
    background: transparent center;
    padding: 0;
    border-radius: 0;
}

#Filters .genre-container label {
    padding: 25px 0 !important;
    border: none !important;
    border-radius: unset !important;
}

#Filters .genre-container label:hover {
    border: none !important;
}

#Filters .genre-container label:hover > img {
    opacity: 1 !important;
}

#Filters .genre-container label:hover > div p {
    opacity: 0 !important;
    padding: 10px 10px;
    letter-spacing: 10px;
    margin-top: -10px;
}

#Filters .genre-container label {
    border: none;
    position: absolute;
    width: 100%;
    height: 100%;
    padding: 0 !important;
    display: block;
    margin: 0 auto;
    text-align: center;
}

#Filters .genre-container div {
    display: block;
    position: absolute;
    margin-top: 25px;
    width: 100%;
    white-space: nowrap;
}

#Filters .genre-container p {
    width: fit-content;
    opacity: 1;
    margin: 0 auto;
    padding: 0 10px;
    font-size: .8rem;
    color: #fff;
    border-bottom: 1px solid rgba(255,255,255,0.3);;
    border-top: 1px solid rgba(255,255,255,0.3);
    background-color: rgba(0,0,0,0.3);
    text-shadow: 1px 1px black;
    transition: .2s;
}

#Filters .genre-container img {
    position: absolute;
    opacity: 0.2;
    object-fit: cover;
    width: 100%;
    height: 100%;
    transition: .2s;
    margin-left: -75px;
}

#Filters .active-genre {
    opacity: 1 !important;
}

#Filters .ordering-container {
    margin-top: 25px;
    text-align: center;
}

#Filters .no-border {
    border: none !important;
}

#Filters .no-hover-color:hover {
    color: #fff !important;

}

#Filters .active-order-label {
    border-color: var(--blue) !important;
}

#Filters .active-order-label:hover {
    border-color: var(--blue) !important;
}

#Filters .ordering-container .input-group {
    border: none;
}

#Filters .ordering-container .input-group .order-label {
    margin-left: 5px;
    text-transform: capitalize;
}

#Filters .ordering-container .input-group .order-buttons-container {
    height: 0;
    padding: 0 !important;
    z-index: 3;
    background-color: var(--black-03);
    position: absolute;
    overflow: hidden;
    white-space: nowrap;
    transition: .2s;
}

#Filters .ordering-container .input-group .order-buttons-container .order-btn {
    width: 35px !important;
    display: inline-block;
}

#Filters .ordering-container .input-group .order-buttons-container .order-btn:first-child {
    border: 1px solid var(--gray);
    border-bottom-left-radius: 5px;
    border-top-left-radius: 5px;
    border-right: none;
}

#Filters .ordering-container .input-group .order-buttons-container .order-btn:nth-child(2) {
    border: 1px solid var(--gray);
    border-bottom-right-radius: 5px;
    border-top-right-radius: 5px;
    border-left: none;
}

#Filters .ordering-container .input-group .order-buttons-container .order-btn label {
    border: none !important;
    font-size: 1.2rem !important;
    color: #fff !important;
}

#Filters .ordering-container .input-group .order-buttons-container .order-btn label:hover {
    color: var(--blue) !important;
}

#Filters .ordering-container .input-group .hovered-order-button-container {
    height: 30px !important;
}

#Filters #filters-collapsable {
    /*transform: scaleY(1);*/
    /*transform-origin: top;*/
    height: 0;
    overflow: hidden;
    transition: .1s;
}

#Filters .show-filters {
    height: auto !important;
}

#Filters .filters-wrapper-active {
    transform: scaleY(0) !important;
    transform-origin: top;
    overflow: hidden;
    transition: .1s;
}

#Filters .filters-wrapper-active-hide {
    height: 0 !important;
}

#Filters .filter-buttons {
    padding: 0 10px;
}

#Filters .filter-buttons button {
    width: 100%;
    margin-top: 15px;
}

#Filters .filter-buttons span {
    font-size: 1.1rem;
}
/* ************* FILTERS ************ */


/*PRESET FILTERS*/
#Preset-Filters {
    margin-top: 12px;
    padding: 0 10px;
}

.settings-buttons {
    padding: 0 !important;
    margin: 3px 0 !important;
}

.settings-buttons button {
    width: 100% !important;
}

#Preset-Filters .preset-settings {
    width: 15%;
    text-align: center;
}

#Preset-Filters .preset-selector {
    width: 85%;
}
/*PRESET FILTERS*/
