.bbcode-container p {
    margin: 0;
    display: inline-block;
}

#full-width-tabpanel-0 .MuiBox-root,
#full-width-tabpanel-1 .MuiBox-root {
    padding: 0;
}

#BBCode-Editor {
    padding-top: 100px;
}

#BBCode-Editor .cyber-tabs {
    width: 33%;
    position: fixed;
    background-color: var(--darker-blue-rgba-095) !important;
}

#BBCode-Editor .cyber-tabs {
    width: 33%;
    position: fixed;
}

#BBCode-Editor .bbcode-editor-segment {
    overflow: auto;
    height: calc(100vh - 100px);
}

#BBCode-Editor .editor-container {
    margin-top: 48px;
    margin-bottom: 20px;
}

#BBCode-Editor .editor-container .form-control {
    margin-top: 10px;
}

#BBCode-Editor .editor-container .form-control:nth-child(2) {
    margin-top: 20px;
}

#BBCode-Editor .submit-btn-container {
    width: 100%;
    text-align: right;
}

#BBCode-Editor .editor-container .form-control {
    width: 100%;
}

#BBCode-Editor .editor-container .form-control p {
    color: var(--gray)
}

#BBCode-Editor .available-tags {
    margin-top: 30px;
    padding: 20px;
}

#BBCode-Editor .available-tags h2 {
    text-indent: 1rem;
}

#BBCode-Editor .available-tags .tag-color {
    color: #849eff;
}

#BBCode-Editor .background-image-segment {
    margin-top: 50px;
}

#BBCode-Editor .form-control-container {
    padding: 10px 5px 5px;
    display: inline-block;
    width: 50% !important;
}

#BBCode-Editor .form-control-submit {
    padding: 10px 5px 5px;
    text-align: right;
}
