#User-List {
    margin-top: 60px;
}

#User-List .list-table img {
    height: 100px;
    width: 70px;
    object-fit: cover;
    transition: .1s;
}

#User-List .list-table .cover-img-link:hover {
    opacity: .6;
}

#User-List .status-buttons button {
    width: 100%;
}

#User-List .status-buttons button .btn-label {
    margin-left: 5px;
}

#User-List .edit-btn {
    margin-left: 5px;
    vertical-align: -1px;
}

#User-List .title-link {
    font-size: 1.1rem;
    color: #fff;
    transition: .1s;
}

#User-List .title-link:hover {
    color: gray;
}

#User-List .table-number {
    font-size: 1.1rem;
}

#User-List .genre {
    margin: 2px;
}

#User-List .table-number {
    padding: 2px 6px 3px;
    white-space: nowrap;
    border-radius: 12px;
}

#User-List .table-number .update-anime-episode-btn {
    margin-left: 5px;
    font-size: 1rem;
}
