#Achievements .achievement-container {
    padding-bottom: 50px;
}

#Achievements h3 {
    font-size: 2rem;
    text-indent: 2rem;
    display: block;
}

#Achievements h5 {
    display: inline-block;
    font-size: 1.25rem;
    text-indent: 1rem;
    margin-bottom: 10px;
}

#Achievements .achievement-progress {
    height: 35px;
    border-radius: 5px;
}

#Achievements .progress-bar .achievement-progress,
#Achievements .progress-bar .progress-number {
    transition: .2s;
}

#Achievements .progress-bar .achievement-progress:hover {
    opacity: .7 !important;
}

#Achievements .progress-bar .achievement-progress:hover + .progress-number {
    opacity: 1 !important;
}

#Achievements .progress-bar .progress-number {
    margin-top: -30px;
    user-select: none;
    pointer-events: none;
}

#Achievements .progress-number {
    width: 25%;
    margin-top: 6px;
    text-align: center;
    position: absolute;
    z-index: 99;
}

#Achievements .progress-number span {
    background-color: var(--darker-blue);
    padding: 5px 10px;
    border-radius: 5px;
}

#Achievements .achievement-image {
    width: 180px;
    height: 100px;
    margin: 10px auto 0;
    text-align: center;
    opacity: .5;
}

#Achievements .achievement-image span {
    width: 100%;
}

#Achievements .achievement-image img {
    width: 100%;
    height: 100%;
    object-fit: cover;
    border-radius: 5px;
}

#Achievements .achievement-image .check-icon {
    display: none;
}

#Achievements .completed {
    opacity: 1;
}

#Achievements .completed .check-icon {
    display: block;
    position: absolute;
    text-align: right;
    margin-top: -37px;
    padding-right: 10px;
    width: 180px;
    font-size: 1.25rem;
    color: var(--blue);
}
