#Display-Franchise {
    overflow: hidden;
    height: 100vh;
    width: 100%;
}

#Display-Franchise .foreign-object {
    text-align: center;
    pointer-events: none;
}

#Display-Franchise .foreign-object button {
    margin-top: 10px;
    pointer-events: initial;
}

#Display-Franchise .card-wrapper {
    width: 300px;
    height: 400px;
    overflow: hidden;
}

#Display-Franchise .card-relation-name {
    text-align: center;
    font-size: 1.5rem !important;
    margin: 0 0 10px;
}

#Display-Franchise .franchise-card {
    position: initial;
    display: block;
    width: 300px;
    height: 400px;
}

#Display-Franchise .franchise-card:hover {
    opacity: 0.5;
    transform: none;
}

#Display-Franchise .franchise-card h3 {
    margin: 0;
    padding-top: 35px;
}

#Display-Franchise .franchise-card .title-card-body {
    margin-top: 220px;
    width: 100%;
    position: initial;
    display: block;
}